<template>
	<NcModal v-if="show"
		:size="modalSize"
		:can-close="false"
		class="assistant-modal"
		@close="onCancel">
		<div ref="modal_content"
			class="assistant-modal--wrapper">
			<div class="assistant-modal--content">
				<span class="assistant-bubble">
					<DogIcon :size="16" class="icon" />
					<span>{{ t('assistant', 'Blablador') }}</span>
				</span>
				<h2>
					{{ taskTypeName }}
				</h2>
				<NcButton :aria-label="closeButtonLabel"
					:title="closeButtonTitle"
					type="tertiary"
					class="close-button"
					@click="onCancel">
					<template #icon>
						<CloseIcon />
					</template>
				</NcButton>
				<div v-if="myOutput"
					class="output">
					<label
						for="assistant-output"
						class="input-label">
						{{ t('assistant', 'Output') }}
					</label>
					<NcRichContenteditable
						id="assistant-output"
						ref="output"
						:value.sync="myOutput"
						class="editable-output"
						:multiline="true"
						:placeholder="t('assistant', 'Output')"
						:link-autocomplete="false" />
					<NcNoteCard
						class="warning-note"
						type="warning">
						{{ t('assistant', 'This output was generated by AI. Make sure to double-check and adjust.') }}
					</NcNoteCard>
					<div class="button-wrapper">
						<NcButton type="secondary"
							:title="t('assistant', 'Copy output text to clipboard')"
							@click="onCopy">
							{{ t('assistant', 'Copy output') }}
							<template #icon>
								<ClipboardCheckOutlineIcon v-if="copied" />
								<ContentCopyIcon v-else />
							</template>
						</NcButton>
						<NcButton :disabled="output === myOutput"
							type="secondary"
							:title="t('assistant', 'Reset the output value to the originally generated one')"
							@click="delayedReset">
							{{ t('assistant', 'Reset') }}
						</NcButton>
					</div>
				</div>
			</div>
		</div>
	</NcModal>
</template>

<script>
import CloseIcon from 'vue-material-design-icons/Close.vue'
import DogIcon from 'vue-material-design-icons/Dog.vue'
import ClipboardCheckOutlineIcon from 'vue-material-design-icons/ClipboardCheckOutline.vue'
import ContentCopyIcon from 'vue-material-design-icons/ContentCopy.vue'

import NcModal from '@nextcloud/vue/dist/Components/NcModal.js'
import NcButton from '@nextcloud/vue/dist/Components/NcButton.js'
import NcRichContenteditable from '@nextcloud/vue/dist/Components/NcRichContenteditable.js'
import NcNoteCard from '@nextcloud/vue/dist/Components/NcNoteCard.js'

import VueClipboard from 'vue-clipboard2'
import Vue from 'vue'

import { showError } from '@nextcloud/dialogs'

import { TASK_TYPES } from '../constants.js'

Vue.use(VueClipboard)

export default {
	name: 'AssistantPlainTextModal',
	components: {
		NcModal,
		NcButton,
		CloseIcon,
		NcRichContenteditable,
		NcNoteCard,
		DogIcon,
		ClipboardCheckOutlineIcon,
		ContentCopyIcon,
	},
	props: {
		output: {
			type: String,
			default: '',
		},
		taskType: {
			type: [Number, null],
			default: null,
		},
	},
	emits: [
		'cancel',
	],
	data() {
		return {
			show: true,
			myOutput: this.output,
			closeButtonTitle: t('assistant', 'Close'),
			closeButtonLabel: t('assistant', 'Close Nextcloud Assistant'),
			modalSize: 'normal',
			copied: false,
		}
	},
	computed: {
		taskTypeName() {
			switch (this.taskType) {
			case TASK_TYPES.text_generation:
				return t('assistant', 'Text Generation')
			case TASK_TYPES.speech_to_text:
				return t('assistant', 'Speech to Text')
			default:
				return t('assistant', 'Unknown Result Type')
			}
		},
	},
	mounted() {
		if (this.myOutput !== '') {
			this.$refs.output.focus()
		}
	},
	methods: {
		onCancel() {
			this.show = false
			this.$emit('cancel')
		},
		async onCopy() {
			try {
				const container = this.$refs.output.$el
				await this.$copyText(this.output.trim(), container)
				this.copied = true
				setTimeout(() => {
					this.copied = false
				}, 5000)
			} catch (error) {
				console.error(error)
				showError(t('assistant', 'Result could not be copied to clipboard'))
			}
		},
		onReset() {
			this.myOutput = this.output
		},

		delayedReset() {
			// This is a hack to ensure the text box is updated
			// when we reset the text since removing newlines or spaces
			// from the end of the text does not trigger an update.

			// Delete any trailing newlines
			this.myOutput = this.myOutput.replace(/\n+$/, '')
			this.myOutput += '.'

			// Let the ui refresh before resetting the text
			setTimeout(() => {
				this.onReset()
			}, 0)
		},
	},
}
</script>

